import {Component, OnInit, ViewChild} from '@angular/core';
import {RestService} from '../../service/rest.service';
import {AuxiliaryList, InterventionByTheme, Sessions, themeSearch} from '../../interfaces/model';
import {Properties} from '../../properties/properties';
import {AlertController, IonInfiniteScroll, ModalController, NavParams, ToastController} from '@ionic/angular';
import { ModalAttachmentPage } from '../modal/modal-attachment/modal-attachment.page';
import {ModalInterveningPage} from '../modal/modal-intervening/modal-intervening.page';
import {ConfiguracionListaTheme, NAME_LIST_THEME, NAME_LIST_THEME_DELETE} from '../../reducers/favThemes/theme.action';
import {Store} from '@ngrx/store';
import {AppState} from '../../app.reducers';
import {ActivatedRoute, Router} from "@angular/router";

@Component({
  selector: 'app-temas',
  templateUrl: './temas.page.html',
  styleUrls: ['./temas.page.scss'],
})
export class TemasPage implements OnInit {
    // @ts-ignore
    @ViewChild(IonInfiniteScroll) infiniteScroll: IonInfiniteScroll;
    searchTheme = '';
    sessionNumber = null;
    dateFrom = null;
    dateTo = null;
    listFilterTheme = false;
    hiddenSearch = false;
    listItemsTheme = false;
    listItemsSearch = true;
    theme: themeSearch[] = [];
    sessionStorage: themeSearch [] = [];
    minDateFrom = new Date('2012-02-28').toISOString();
    maxDateFrom = new Date().toISOString();
    minDateTo = new Date('2012-02-28').toISOString();
    maxDateTo = new Date().toISOString();

    i = 0;
    j = 0;
    l = 0;
    k = 0;
    m = 0;

    banValidate = false;
    themeStar: boolean[] = [];

    args: any;

  constructor(private getRest: RestService,
              public properties: Properties,
              private modalController: ModalController,
              private alertController: AlertController,
              private store: Store<AppState>,
              public toastController: ToastController,
              private route: ActivatedRoute
  ) {}

  ngOnInit() {
  }
    ionViewWillEnter() {
      const idTheme = this.route.snapshot.queryParamMap.get('id');
      if (idTheme !== null) {
          this.listItemsSearch = false;
          this.listItemsTheme = true;
          this.infiniteScroll.disabled = true;
          this.hiddenSearch = true;
          this.getThemeById(idTheme);
      } else {
          this.listItemsSearch = true;
          this.listItemsTheme = false;
          this.searchTheme = '';
          this.theme = [];
      }

    }
    /*
    Método que consulta un tema por ID desde el componente de Sesiones.
     */
    getThemeById(idTheme) {
      this.getRest.getThemes({
          id: idTheme,
          search: '',
          sessionNumber: '',
          dateFrom: '',
          dateTo: '',
          offset: 0
      }).subscribe( response => {
          this.theme = response;
      });
    }

    loadData(event) {
        setTimeout(() => {
                this.changeSearch(this.searchTheme, event);
        }, 500);

    }
    changeSearch(textSearch?: string, event?) {
        let df = '';
        let dt = '';
        let sn = '';
        if ( this.dateFrom != null ) {
            df = this.dateFrom.toString().substr(0, 10);
        }
        if ( this.dateTo != null ) {
            dt = this.dateTo.toString().substr(0, 10);
        }
        if ( this.sessionNumber != null ) {
            sn = this.sessionNumber.toString().substr(0, 10);
        }
        this.k = 0;
        const incre = 30;
        if (event !== undefined) {
            this.j = this.j + incre;
        }
        this.searchTheme = textSearch;
        // Reset items back to all of the items
        if (textSearch !== '' || sn !== '' || df !== '' || dt !== '') {
            this.listItemsSearch = false;
            this.listFilterTheme = false;
            this.listItemsTheme = true;
            this.getRest.getThemes({
                    id: '',
                    search: this.searchTheme,
                    sessionNumber: sn,
                    dateFrom: df,
                    dateTo: dt,
                    offset: this.j
            }
            ).subscribe(response => {
                if (response.length > 0) {
                    if (event !== undefined) {
                        this.theme.push(...response);
                        this.theme.forEach(data => {
                            this.valFavorites(data['description'], this.k);
                            this.k++;
                        });
                        if (event) {
                            event.target.complete();
                        }
                    } else {
                        this.theme = response;
                        this.theme.forEach(data => {
                            this.valFavorites(data['description'], this.k);
                            this.k++;
                        });
                    }
                } else {
                    if (response.length === 0) {
                        event.target.disabled = true;
                    }
                }
            });
        } else {
            this.listItemsTheme = false;
            this.listItemsSearch = true;
            this.listFilterTheme = true;

        }
    }
    resetTextFirst() {
        this.searchTheme = '';
        this.getChange();
    }
    resetText() {
        this.sessionNumber = null;
        this.getChange();
    }
    resetFrom() {
        this.dateFrom = null;
        this.getChange();
    }
    resetTo() {
        this.dateTo = null;
        this.getChange();
    }
    /*getClear() {
        this.i = 0;
        this.j = 0;
        this.searchTheme = '';
        this.theme = [];
        this.listItemsTheme = false;
        this.listItemsSearch = true;
    }*/
    getChange() {
        if (this.dateFrom !== null) {
            this.minDateTo = new Date(this.dateFrom).toISOString();
        } else {
            this.minDateTo = new Date('2012-02-28').toISOString();
        }
        if (this.dateTo !== null) {
            this.maxDateFrom = new Date(this.dateTo).toISOString();
        } else {
            this.maxDateFrom = new Date().toISOString();
        }
        this.i = 0;
        this.j = 0;
        this.theme = [];
        this.infiniteScroll.disabled = false;
        this.changeSearch(this.searchTheme, undefined);
    }

    async openAttachments(t: themeSearch) {
        const modal = await this.modalController.create({
            component: ModalAttachmentPage,
            componentProps: {
                "sessionId": t.agendaId,
                "themeId": t.id
            }
        });
        return await modal.present();
    }
    getIntervening(t: themeSearch) {
        this.getRest.getInterveningByTheme({
            themeId: t.id
        }).subscribe(response => {
            let themeList: AuxiliaryList[] = [];
            let lst: InterventionByTheme[];
            lst = response;
            if (lst.length > 0) {
                const arr = lst.map(p => p.themeId);
                const s = new Set(arr);
                const u = Array.from(s);
                for( const n of u ){
                    let al: AuxiliaryList = {
                        description: '',
                        id: 0,
                        startdate: new Date(),
                        list: []
                    };
                    al.id = n;
                    for(const it of lst){
                        if( it.themeId === n ){
                            al.description = it.description;
                            break;
                        }
                    }
                    themeList.push(al);
                }
                this.openIntervening(lst, themeList);
            } else {
                this.presentToast('No existen intervenciones para el tema');
            }
        });
    }
    async openIntervening(l: InterventionByTheme[], t: AuxiliaryList[]) {
        const modal = await this.modalController.create({
            component: ModalInterveningPage,
            componentProps: {
                "list": l,
                "themes": t
            }
        });
        return await modal.present();
    }

    async presentAlert(note) {
        const alert = await this.alertController.create({
            message: note,
            buttons: ['Aceptar']
        });

        await alert.present();
    }
    putTheme(item: themeSearch, index) {
        this.banValidate = false;
        this.sessionStorage = JSON.parse(localStorage.getItem('theme'));
        if (this.sessionStorage) {
            for (this.m = 0; this.m < this.sessionStorage.length; this.m++) {
                if (this.sessionStorage[this.m].description === item.description) {
                    // this.presentToast('El tema ya se encuentra en favoritos');
                    this.removeTheme(item, index);
                    this.banValidate = false;
                    return;
                } else {
                    this.banValidate = true;
                }
            }
        } else {
            this.banValidate = true;
        }
        if (this.banValidate === true) {
            this.themeStar[index] = true;
            this.presentToast('Se añadio a favoritos el tema seleccionado');
            const newListaTheme = new ConfiguracionListaTheme(item.agendaId,
                item.agendaNumber, item.agendaStartDate, item.approvedOrder, item.dates, item.description, item.id);
            this.store.dispatch({type: NAME_LIST_THEME , text: newListaTheme});
        }
    }

    removeTheme(item: themeSearch, index) {
        const deleteList = new ConfiguracionListaTheme(item.agendaId,
            item.agendaNumber, item.agendaStartDate, item.approvedOrder, item.dates, item.description, item.id);
        this.store.dispatch({type: NAME_LIST_THEME_DELETE, text: deleteList});
        this.themeStar[index] = false;
        this.presentToast('Tema eliminado de la sección de favoritos');
    }

    async presentToast(msg) {
        const toast = await this.toastController.create({
            message: msg,
            duration: 2000,
            position: 'bottom'
        });
        toast.present();
    }
    /*
        Método que indica en el icono star que temas estan guardados en favoritos
     */
    valFavorites(data, index) {
        this.sessionStorage = JSON.parse(localStorage.getItem('theme'));
        if (this.sessionStorage) {
            for (this.l = 0; this.l < this.sessionStorage.length; this.l++ ) {
                if (this.sessionStorage[this.l].description === data) {
                    this.themeStar[index] = true;
                    this.l = this.sessionStorage.length;
                } else {
                    this.themeStar[index] = false;
                }
            }
        }
    }

}
